@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.header {
  background: #FFFFFF;
  box-shadow: 0px 1px 3px 0px rgba(130, 130, 130, 0.5);
}
.middleContent {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  margin-bottom: 40px;
  margin-top: 20px;
}
.middleContent .pagePreview {
  position: relative;
  width: 420px;
  height: 680px;
  background: #FFFFFF;
  box-shadow: 0px 2px 10px 0px rgba(190, 190, 190, 0.5);
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.middleContent .bannerContainer {
  display: flex;
  flex-direction: column;
  margin-left: 40px;
}
.middleContent .bannerContainer .banner {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.middleContent .bannerContainer .banner .platformTitle {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 20px;
  font-weight: 500;
  height: 28px;
  line-height: 28px;
  color: #1A0000;
}
.middleContent .bannerContainer .banner .createdInfo {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  font-size: 14px;
  font-weight: 200;
  line-height: 19px;
}
.middleContent .bannerContainer .banner .createdInfo .createdOn {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  color: #6C7579;
}
.middleContent .bannerContainer .banner .createdInfo .createdTime {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  color: #1F0021;
}
.middleContent .bannerContainer .banner .divider {
  width: 100%;
  height: 3px;
  margin-top: 10px;
  -webkit-transform: scaleY(-1);
          transform: scaleY(-1);
  background: #FCBE31;
}
.middleContent .bannerContainer .shareButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 45px;
  background: #FFFFFF;
  box-shadow: 0px 2px 10px 0px rgba(190, 190, 190, 0.5);
  border-radius: 2px;
  margin-top: 35px;
}
.middleContent .bannerContainer .shareButton .shareIcon {
  width: 16px;
  height: 16px;
  background-image: url(data:image/png;base64,);
  background-size: 100%;
}
.middleContent .bannerContainer .shareButton .shareText {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 16px;
  color: #1A0000;
  font-weight: 500;
  margin-left: 20px;
}
@media screen and (max-width: 524px) {
  .middleContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 16px;
    margin-top: 18px;
  }
  .middleContent .pagePreview {
    width: 100%;
    height: auto;
    zoom: 0.92;
    box-shadow: none;
  }
  .middleContent .bannerContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 92%;
    margin-left: 15px;
    margin-bottom: 20px;
  }
  .middleContent .bannerContainer .banner .platformTitle {
    font-size: 12px;
    height: 16px;
    line-height: 16px;
  }
  .middleContent .bannerContainer .banner .createdInfo {
    margin-top: 7px;
    font-size: 10px;
    line-height: 13px;
    height: 13px;
  }
  .middleContent .bannerContainer .banner .divider {
    height: 2px;
    margin-top: 7px;
  }
  .middleContent .bannerContainer .shareButton {
    margin-top: 0;
    width: 50%;
  }
}
